import copy from 'clipboard-copy';
import { formatEthFromWei, formatNumber } from './Formatters';

//  Global setup Web3 Contract-Interaction
//
import abi from "./assets/abi/voting-contract.json";
import TopAppBar from './components/AppBar';
import ConnectButton from './components/ConnectButton';

import taxMeterImg from './assets/img/tax-meter.png'
import {ReactComponent as TelegramIcon} from './assets/img/Telegram_logo.svg';
import SendIcon from '@mui/icons-material/Send';

import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';



//const contractAddress: string = '0x6B4E65FB66F49655cBe8B4228319bB2c9E26aad8'; // release on mainnet
//const contractAddress: string = '0x5372579dc14033a8B6Ed8aB4120781B33bBB711b'; // release on mainnet 2022
const contractAddress: string = '0x4af53af7784f1a67f72fd623EB0Ab8C79aDA3700'; // release on mainnet 2023

//window.chainIdExpected = 97;
window.chainIdExpected = 56;


declare global {
  interface Window {
    dataRefreshInterval: number,
    farm_contract: ContractContext;
    web3: Web3;
    walletConnectProvider: WalletConnectProvider,
    ethereum: any;
    contractAddress: string;
    contract_abi: AbiItem[];
    walletAddress: string;
    connected: boolean;
    refferalAddress: string; // the ref address that invited me
    chainId: number;
    chainIdExpected: number;
    bnbPrice: number;
  }
}

interface AppProps {
  style?: React.CSSProperties;
}


const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.h3,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

window.dataRefreshInterval = 3000;

class App extends React.Component<AppProps> {
  
  state = {
    radioValue: "",
    progress: false,
    walletAccepted: false,
    alreadyVoted: false,
    totalYES: 0,
    totalNO: 0,
    totalVotes: 0,
    votingEnded: false
  }

  baseState: any;
  _stateInterval: any;


  constructor(props: AppProps) {
    super(props);

    this.baseState = this.state;
    // properties


  }


  componentDidMount = () => { 
    window.contractAddress = contractAddress;
    window.contract_abi = abi as AbiItem[];


    this.checkState();

    if(this._stateInterval){
      clearInterval(this._stateInterval);
    }
    this._stateInterval = setInterval(this.checkState, window.dataRefreshInterval);

    //this.setModalOpen();
  }


  componentWillUnmount = () => { 
    
  }

  
  checkState = () => {  

    if(window.connected !== true){
      this.setState(this.baseState);
      return;
    }

    this.setState({
      yourAddress: window.walletAddress,
    });

    window.farm_contract.methods.getWalletStatus(window.walletAddress).call().then((result: boolean) => {
      //console.log(result);
      this.setState({
        walletAccepted: result
      });
    });


    window.farm_contract.methods.getUserAnswer(window.walletAddress).call().then((result: string) => {
      //console.log(result);
      this.setState({
        alreadyVoted: result != "0"
      });
    });

    window.farm_contract.methods.getTotalAnswers("1").call().then((resultYes: string) => {
      //console.log(result);
      let totalYES = parseInt(resultYes);


      window.farm_contract.methods.getTotalAnswers("2").call().then((resultNO: string) => {
        //console.log(result);
        let totalNO = parseInt(resultNO);

        this.setState({
          totalYES: (100 / (totalYES + totalNO) * totalYES),
          totalNO: (100 / (totalYES + totalNO) * totalNO),
          totalVotes: totalYES + totalNO
        });

        window.farm_contract.methods.started().call().then((started: boolean) => {
          //console.log(result);

          if(started == false && (totalYES + totalNO) > 0){
            this.setState({
              votingEnded: true
            });
          }


        });
        
      });
    });


  }

  onClick_Telegram = () => {
    window.location.href = "https://t.me/PISTON_TOKEN";
  }

  onClick_Submit = () => {
    //alert(this.state.radioValue)
    let openValue = "";
    if(this.state.radioValue.toLowerCase() == "op1"){
      openValue = "1";
    }else if(this.state.radioValue.toLowerCase() == "op2"){
      openValue = "2";
    }

    this.setState({
      progress: true
    });

    window.farm_contract.methods.vote(openValue).send({from: window.walletAddress}).then((response) => {
      this.setState({
        progress: false
      });
    }).catch((err:any) => {
      alert("transaction failed");
      this.setState({
        progress: false
      });
    });
  }

  handleRadioChange = (event: any, _value: string) => {
    this.setState({
      radioValue: _value
    })
  }

  //  Main Layout
  //
  render = () => {
    return (
      <div>
      <div className={"wrapper"}>
        <img className={"logo"} src={"https://piston-token.com/images/logo.svg"}/>
        <h2>PISTON TOKEN</h2>
        <h3>final claim</h3>



      </div>
  <a id="details"></a>
      <div className={"wrapper left"} style={{marginTop: "16px"}}>
        11th November<br/><br/>
      Greetings all! The time is finally here! ü§òüèº<br/>
As you all know, PSTN unfortunately came to an end. We ran a vote many months ago, and 81% of the community decided to invest the little liquidity that remained to gain profits and hopefully start giving back to all those that did not receive their original investment back. 
<br/><br/>
Our first investment did not go well but we didn‚Äôt give up. A new opportunity arose, at a lower % but much safer so we decided to go with it and from there we‚Äôve been doing very well.
Please remember that this is unprecedented, no other projects have done something like this, and we are proud to finally be at a stage where we can give back.
<br/><br/>
A new dapp has been created from which you will be able to withdraw $20 per day per wallet (only whitelisted wallets of course). Users will be able to do this until the claim pool reaches zero. We will continue to fill the pool ideally twice a month in increments of $500 for the foreseeable future. 
<br/><br/>
The target is to increase the injections with time as our investment continues to compound and grow.
<br/><br/>
There are only TWO members of the original PSTN team left and we will NEVER DM you with links, wallet connect pages or other strange requests. Please always be careful and ensure you‚Äôre talking to the correct person by checking their username. The link for the site will be posted by ME on THIS group ONLY.
<br/><br/>
As a token of appreciation for all those that stayed with us and believed we could pull this off, the first injection will be of $1000.
<br/><br/>
This weekend we will be running tests on the dapp on testnet to ensure there are no issues and will hopefully deploy the dapp for you all on Monday or Tuesday if everything goes well.
<br/><br/>
Thank you.
      </div>

      <Button fullWidth={true} variant={"outlined"} color={"secondary"} sx={{my: 1, marginRight: 1}} size="small" onClick={this.onClick_Telegram}><TelegramIcon style={{height: "30px", marginRight: "8px"}}></TelegramIcon>Telegram</Button>     
        
      </div>
    );
  }
}

export default App;
